import { Axios } from 'axios'
import { Page, stringify, uriFy } from '../core'

export class ApiEntity {
  protected axios

  // Pagination
  protected defaultLimit = 50
  protected limit = 200

  constructor(axios: Axios) {
    this.axios = axios
  }

  /**
   * Create item by body
   * @param url - the url to use
   * @param body - the body to send
   * @returns the promise for create the item
   */
  protected async _create<T>(url: string, body: any): Promise<T> {
    return this.axios
      .request({
        url,
        method: 'POST',
        data: stringify(body),
      })
      .then((response) => response.data)
  }

  /**
   * Search items by params
   * @param url - the url to use
   * @param searchParams - the search params to filter the items
   * @returns the promise for search items
   */
  protected async _search<T>(
    url: string,
    searchParams?: any
  ): Promise<Page<T>> {
    return this.axios
      .request({
        url,
        method: 'GET',
        responseType: 'json',
        params: searchParams
          ? new URLSearchParams(uriFy(searchParams))
          : undefined,
      })
      .then((response) => response.data)
  }

  /**
   * Search ALL items by params
   * @param url - the url to use
   * @param searchParams - the search params to filter the items
   * @returns the promise for search all items
   */
  protected async _searchAll<T>(
    url: string,
    searchParams: any = {}
  ): Promise<Page<T>> {
    let offset = 0
    let totalCount = this.limit
    let data = [] as T[]

    do {
      const response = await this._search<T>(url, {
        ...searchParams,
        limit: this.limit,
        offset,
      })
      offset += this.limit
      totalCount = response.totalCount
      data = [...data, ...response.data]
    } while (offset <= totalCount)

    return {
      data,
      totalCount,
    }
  }

  /**
   * List items by params
   * @param url - the url to use
   * @param searchParams - the search params for items filtering
   * @returns the promise for list items
   */
  protected async _list<T>(url: string, searchParams?: any): Promise<T[]> {
    return this._search<T>(url, searchParams).then((response) => response.data)
  }

  /**
   * List ALL items by params
   * @param url - the url to use
   * @param searchParams - the search params for items filtering
   * @returns the promise for list items
   */
  protected async _listAll<T>(url: string, searchParams?: any): Promise<T[]> {
    return this._searchAll<T>(url, searchParams).then(
      (response) => response.data
    )
  }

  /**
   * Find one item by search params if exists
   * @param url - the url to use
   * @param searchParams - the search params for item search
   * @returns the promise for find an item
   */
  protected async _findOne<T>(
    url: string,
    searchParams?: any
  ): Promise<T | undefined> {
    return this.axios
      .request({
        url,
        method: 'GET',
        params: searchParams
          ? new URLSearchParams(uriFy(searchParams))
          : undefined,
      })
      .then((response) => {
        const page = response.data
        return page.data ? page.data[0] : undefined
      })
  }

  /**
   * Read an item by params. Emit an error otherwise
   * @param url - the url string
   * @param searchParams - the search params to use
   * @returns the promise for read an item
   */
  protected async _readOne<T>(url: string, searchParams: any): Promise<T> {
    return this.axios
      .request({
        url,
        method: 'GET',
        params: new URLSearchParams(uriFy(searchParams)),
      })
      .then((response) => {
        const { data } = response.data

        if (!data.length) {
          throw Error('Not found')
        }

        return data[0]
      })
  }
}
